<template>
  <div class="home">
    <section class="hero is-medium is-dark mb-6">
      <div class="hero-body has-text-centred">
        <p class="title mb-6">
          WELCOME TO GSOCO MALL
        </p>
        <p class="title mb-6">
          Your best online store
        </p>
      </div>
    </section>
    
    <div class="column is-multiline">
      <div class="column is-12">
        <h2 class="is-size-2 has-text-centred">
          Latest Products
        </h2>
      </div>
      
      <ProductBox 
                v-for="product in latestProducts"
                v-bind:key="product.id"
                v-bind:product="product" 
                />
    </div>
  </div>
</template>

<script>
import axios from 'axios'

import ProductBox from '@/components/ProductBox'

export default {
  name: 'Home',
  metaInfo: {
    title: 'Home',
    titleTemplate: '%s | GSOCO MALL',
    htmlAttrs: {
      lang: 'en-US'
    },
    meta: [
      { charset: 'utf-8' },
      { name: 'description', content: 'Welcome to GSOCO MALL, your best on campus online store.' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1' }
    ]
  },
  data() {
    return {
      latestProducts: []
    }
  },
  components: {
    ProductBox,
  },
  mounted() {
    this.getLatestProducts()
  },
  methods: {
    getLatestProducts() {
      axios
        .get('/api/v1/latest-products/')
        .then(response => {
          this.latestProducts = response.data
        })
        .catch(error => {
          console.log(error)
        })
    }
  },
}
</script>

